	import uniLoadMore from '../../components/uni-load-more/uni-load-more.vue'  // 加载更多(需下载组件)
	
    export default {
		
		components:{uniLoadMore},
		
        data() {
            return {
                news: []
            };
        },
        onLoad:function(){
            this.getNewsList();
        },
		//下拉刷新
		onPullDownRefresh(){
			this.getNewsList();
		},
        methods:{
            opennews(e){
                uni.navigateTo({
                    url: '../info/info?postid='+e.currentTarget.dataset.postid
                });
            },
			getNewsList(){
				uni.request({
				    url: 'https://unidemo.dcloud.net.cn/api/news',
				    method: 'GET',
				    data: {},
				    success: res => {
				        this.news = res.data;
				    },
				    fail: () => {},
				    complete: () => {}
				});
			}
        }
    }
